
import assembly from './assembly'
import Minput from './input'
export default class MInputNumber extends assembly {
  constructor(
    type = 'TableForm',
    name = '',
    col = 24,
    key = 'TableForm' + new Date().getTime()
  ) {
    super(type, name, col, key)
    this.showFrom = ['name', 'col', 'key', 'border', 'stripe']

    this.border = true
    this.stripe = false
    // 
    this.rules = []
    // 
    this.column = [new Minput()]
    // { key: 'name', type: 'input',label: '', rules:[{ required: true, message: '', trigger: 'change'}] },
    // { key: 'name2', type: 'input',label: '2', rules:[{ required: true, message: '', trigger: 'change'}] },
  }
  addcolumn(data) {
    this.column.push(data)
  }
  deletecolumn(id) {
    this.column = this.column.filter(item => item.id !== id)
  }
}
